import React, {Component} from 'react';
import ReactDOM from 'react-dom';


import ProjectPageComponent from './components/ProjectPageComponent';

const images = [
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc.png?raw=true'
    },
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/thankyou.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/thankyou.png?raw=true'
    },
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/webdesign.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/webdesign.png?raw=true'
    },
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux1.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux1.png?raw=true'
    },
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux2.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux2.png?raw=true'
    },
    {
        original: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux3.png?raw=true',
        thumbnail: 'https://github.com/qianabarton/qbdesign.co/blob/master/src/img/projects/misc/flux3.png?raw=true'
    }

];

export default class WebDesign extends Component {

    componentDidMount() {
        ReactDOM.findDOMNode(this).scrollIntoView();
      }

    render() {
        return (
            <div>
               <ProjectPageComponent
                    title="Misc. Design Projects"
                    shortdescription="A collection of some of my random design shots."
                    tags="Branding / Web Design"
                    images={images}
                    description="Coming soon."
                    prevlink="project-link"
                    prev="/cercacor1"
                    nextlink="project-link"
                    next="/sporttrade"
               />
            </div>
        );
    }
}


